import './index.scss';
import styled from 'styled-components';
import { useMediaQuery } from '@mui/material'

export interface TooltipValue {
  color: String;
  colorChanging?: boolean;
  text: String;
  value: String; // attributes name
  changingRate?: String; // attributes name
  prefix?: String;
  suffix?: String;
}

export interface TooltipMiniProps {
  active: any;
  payload: any[];
  value: TooltipValue[];
}

export interface TooltipProps {
  active: any;
  payload: any[];
  day?: boolean | undefined;
  hours?: boolean | undefined;
  value: TooltipValue[];
}

const Dot = styled.div`
  box-sizing: border-box;
  width: 10px;
  height: 10px;
  border: 2px solid ${props => props.color};
  border-radius: 50%;
  display: inline-block;
  margin-right: 8px;
`

const weekday = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]

export const Tooltip = ({
  active,
  payload,
  day = false,
  hours = false,
  value,
  ...props
}: TooltipProps) => {
  const mobile = useMediaQuery('(max-width:1199px)');
  if (active && payload && payload.length) {
    const data = payload[0].payload
    const date = new Date(data.timestamp_sec * 1000)

    return (
      <div className={['graph-tooltip', mobile ? ' mobile' : ''].join(' ')}>
        <div className=''>
          {day && weekday[date.getDay()] + ', '}
          {date.toLocaleDateString('en-us')}
          {hours && <div className={['tooltip-float-right', mobile ? ' mobile' : ''].join(' ')}>
            {date.toLocaleTimeString('en-us')}
          </div>}
        </div>
        {value.map(item => <div className=''>
          {!mobile && <Dot color={item.color} />}
          {item.text}:
          <div className={['tooltip-float-right', mobile ? ' mobile' : ''].join(' ')} style={{ color: item.colorChanging ? data.percentage >= 0 ? '#11ffa9' : '#ff9a9a' : 'white' }}>
            {item.prefix}
            {item.suffix === "%" ? Math.round(data[item.value] * 10000) / 100 : data[item.value]}
            {item.changingRate && `(${data[item.changingRate] * 100}%)`}
            {item.suffix}
          </div>
        </div>)}
      </div>
    );
  }
  return null;
};

export const TooltipMini = ({
  active,
  payload,
  value,
  ...props
}: TooltipMiniProps) => {
  if (active && payload && payload.length) {
    const data = payload[0].payload
    const date = new Date(data.timestamp_sec * 1000)
    return (
      <div className={'graph-tooltip mini'}>
        <div className='relative'>
          <span style={{ color: data.percentage >= 0 ? '#11ffa9' : '#ff9a9a' }}>
            {value[0].prefix}
            {value[0].suffix === "%" ? Math.round(data[value[0].value] * 10000) / 100 : data[value[0].value]}
            {value[0].suffix}
          </span>
          <div className={'tooltip-float-right'} >
            {date.toLocaleDateString('en-us')}
          </div>
        </div>
      </div>
    );
  }
  return null;
};
